body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
nav {
  padding: 0 15rem;
}

.products {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
  padding: 0 10%;
}

.card-image img {
  height: 150px;
  width: 100%;
  display: block;
}

.card {
  display: flex;
  flex-direction: column;
}

.card-content {
  flex: 1;
  padding: 24px;
}

.card-action {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.icon {
  font-size: 2rem;
  color: royalblue;
}

.stars {
  padding-left: 20px;
}

.btn {
  font-size: 14px;
  width: 100px;
  background-color: royalblue;
}

.price {
  font-size: 16px;
}

.card-title {
  font-family: "Roboto", sans-serif;
}

.card-category {
  font-family: "Roboto", sans-serif;
  font-weight: bold;
}

.header {
  background-color: black;
  padding: 30px;
  text-align: center;
}

a {
  color: white;
  font-size: 20px;
  padding: 0 10%;
}
